import PressImage from 'press-ui/press-image/press-image.vue';
import PressCol from 'press-ui/press-col/press-col.vue';
import PressLoadingPlus from 'press-ui/press-loading-plus/press-loading-plus.vue';
import PressRow from 'press-ui/press-row/press-row.vue';
import ImagePreview from 'press-ui/press-image-preview/press-image-preview.vue';
import { imagePreviewHandler } from 'press-ui/press-image-preview/index';


const MODE_LIST = [
  [
    {
      mode: 'aspectFit',
      titleClass: 'image-title--gap',
      span: 8,
    },
    {
      mode: 'aspectFill',
      titleClass: 'image-title--gap',
      span: 8,
    },
    {
      mode: 'center',
      titleClass: '',
      span: 8,
    },
  ],
  [
    {
      mode: 'widthFix',
      titleClass: 'image-title--gap',
      span: 8,
    },
    {
      mode: 'heightFix',
      titleClass: 'image-title--long',
      span: 16,
    },
  ],
];


export default {
  i18n: {
    'zh-CN': {
      error: '加载失败提示',
      loading: '加载中提示',
      defaultTip: '默认提示',
      customTip: '自定义提示',
      mode: '模式',
      round: '圆形',
    },
    'en-US': {
      error: 'Error',
      loading: 'Loading',
      defaultTip: 'Default',
      customTip: 'Custom',
      mode: 'Mode',
      round: 'Round',
    },
  },
  components: {
    PressCol,
    PressRow,
    PressImage,
    PressLoadingPlus,
    ImagePreview,
  },
  data() {
    return {
      src: 'https://mike-1255355338.cos.ap-guangzhou.myqcloud.com/article/2023/10/own_mike_a17f94292a34e9f644.jpeg',
      MODE_LIST,
    };
  },
  methods: {
    click() {
      console.log('[click]');
      imagePreviewHandler({
        selector: '#imagePreviewRef',
        images: [this.src],
      }).then(() => {
      });
    },
    getIndex(a, b) {
      return `${a}-${b}`;
    },
  },
};
